/* Default Custom Cursor */
.custom-cursor {
    display: none; /* Initially hide cursor */
}

/* Hide Default Cursor */
body {
    cursor: none !important;
}

/* Apply styles only when width > 1200px */
@media screen and (min-width: 900px) {
    .custom-cursor {
        position: fixed;
        width: 30px;
        height: 30px;
        background-color: #fff;
        border-radius: 50%;
        z-index: 9999;
        display: block; /* Show cursor when width > 1200px */
        mix-blend-mode: difference;
    }

    /* Hide Default Cursor */
    body {
        cursor: none !important;
    }
}
