/* Menu.css */
.menu-container {
    @apply fixed top-0 left-0 w-full h-full bg-white dark:bg-zinc-950 flex justify-center items-center z-50 ;
}

.menu-content {
    @apply p-8 rounded-lg shadow-lg h-screen w-screen;
}

.logo {
    @apply font-extrabold  cursor-pointer mb-4 fixed top-4 left-4;
}

.cross {
    @apply absolute top-4 right-4 cursor-pointer ;
}

.menu-links {
    @apply lg:grid lg:grid-cols-2 gap-3 w-full h-full text-center items-center flex flex-col justify-center;
}

.menu-links > div {
    @apply mb-4 lg:text-8xl text-5xl;
}

.navNum {
    @apply font-bold text-base mb-2 flex justify-center items-center;
}

.navTitle {
    @apply font-bold  mb-4;
    @apply no-underline ;
}


.navTitle:hover {
    @apply text-blue-500;
}
