@tailwind base;
@tailwind components;
@tailwind utilities;

@layer base {
    html {
        font-family: "Proxima Nova", system-ui, sans-serif;
    }
}

.title {
    @apply font-bold text-8xl h-1/3 flex items-center justify-center font-extrabold text-center
}


/* width */
::-webkit-scrollbar {
    width: 5px;
    border-radius: 60px;
}

/* Track */
::-webkit-scrollbar-track {
    background: #000000;
}

/* Handle */
::-webkit-scrollbar-thumb {
    background: #888;
}

/* Handle on hover */
::-webkit-scrollbar-thumb:hover {
    background: #555;
}

body {
    overflow-x: hidden;
    margin: 0;
    padding: 0;
}
